/* ContractsStyles.css for styles for Contacts.jsx */

body {
  background-color: #080808;
background-image: linear-gradient(45deg, rgba(110, 13, 13, 0.164), rgba(46, 37, 37, 0.075));
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: row;
  background-color: #1f1d1d;
  padding: 20px;
  margin-bottom: 20px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  border: 3px solid #414141;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.2);
}


.container .text {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.container img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
  border: 3px solid #414141;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.2);
}

h1 {
  color: #ffffff;
  font-size: 32px;
  margin-bottom: 5px;
  display: block;
  text-align: center;
  margin-bottom: 20px;
}

h2 {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 5px;
  display: block;
}

h3 {
  color: #ffffff;
  font-size: 18px;
  margin-bottom: 5px;
  display: block;
}

p {
  color: #ffffff;
  font-size: 16px;
  margin-bottom: -10px;
  display: block;
  margin-bottom: 10px;
}

.leader, .monster, .friend {
  font-weight: bold;
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
  max-width: fit-content;
}

.leader {
  color: #0000ca;
  background-color: #e6e6ff;
}

.monster {
  color: #ca0000;
  background-color: #ffe6e6;
}

.friend {
  color: #00ca00;
  background-color: #e6ffe6;
}